n    color: #333333;\r\n    box-sizing: border-box;\r\n}\r\n\r\n*:focus {\r\n    outline: 1px #ED533E80 solid;\r\n}\r\n\r\nbody {\r\n    background-image: radial-gradient( circle farthest-corner at 5.3% 17.2%,  rgba(255,208,253,1) 0%, rgba(255,237,216,1) 90% );\r\n}\r\n\r\n#container {\r\n    display: grid;\r\n    grid-template-columns: 250px auto;\r\n    grid-template-rows: 100px auto auto;\r\n    height: 100vh;\r\n}\r\n\r\nh1 {\r\n    color: #333333;\r\n    font-weight: 400;\r\n    font-size: 32pt;\r\n    letter-spacing: 0.2px;\r\n    padding-bottom: 40px;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n    color: #ED533E;\r\n}\r\n\r\n/* HEADER */\r\n\r\nheader {\r\n    grid-column: 1 / 3;\r\n    grid-row: 1 / 2;\r\n    text-align: center;\r\n    height:120px;\r\n}\r\n\r\n#logo {\r\n    width: 300px;\r\n}\r\n\r\nh3 {\r\n    color: #ED533E;\r\n    font-weight: 400;\r\n    margin-top: -40px;\r\n    font-size: 10pt;\r\n}\r\n\r\n/* SIDEBAR */\r\n\r\naside {\r\n    grid-column: 1 / 2;\r\n    grid-row: 2/ 3;\r\n    display: flex;\r\n    flex-direction: row;\r\n    flex-wrap: wrap;\r\n    align-content: center;\r\n}\r\n\r\n.nav-link {\r\n    width: 100%;\r\n    height: 50px;\r\n    display: flex;\r\n    align-items: center;\r\n    cursor: pointer;\r\n    opacity: 0.5;\r\n}\r\n\r\n.nav-link:hover {\r\n    border-left: 3px #333 solid;\r\n    background-color: #00000013;\r\n    font-weight: bold;\r\n    opacity: 1;\r\n}\r\n\r\n.nav-active {\r\n    border-left: 3px #333 solid;\r\n    background-color: #00000013;\r\n    font-weight: bold;\r\n    opacity: 1;\r\n}\r\n\r\n.nav-link a {\r\n    padding-left: 10px;\r\n    color: #333;\r\n}\r\n\r\n.icon-nav {\r\n    height: 20px;\r\n    font-size: 8pt;\r\n    padding-left: 30px;\r\n    \r\n}\r\n\r\n\r\n/* MAIN */\r\n\r\nmain {\r\n    grid-column: 2/3;\r\n    grid-row: 2/4;\r\n    height: calc(100vh - 160px);\r\n    margin-right: 40px;\r\n    padding-top: 40px;\r\n    padding-left: 30px;\r\n}\r\n\r\n#new-links {\r\n    flex-direction: row-reverse;\r\n    display: flex;\r\n}\r\n\r\n#new-links a {\r\n    height: 20px;\r\n    color: #ED533E;\r\n    font-weight: 600;\r\n    font-size: 12pt;\r\n    text-decoration: none;\r\n    padding-right: 10px;\r\n}\r\n\r\n.icon-20 {\r\n    height: 20px;\r\n}\r\n\r\n#new-links img {\r\n    cursor: pointer;\r\n}\r\n\r\n#new-project-link, #new-task-link {\r\n    display: flex;\r\n    align-items: center;\r\n    align-content: center;\r\n    margin-left: 40px;\r\n    cursor: pointer;\r\n}\r\n\r\n/* cards */\r\n#content {\r\n    display: grid;\r\n    grid-template-rows: auto auto;\r\n    grid-template-columns: auto auto auto;\r\n    gap: 40px;\r\n}\r\n\r\n.card {\r\n    background-color: #ffffff;\r\n    border: 1px #DFE0EB solid;\r\n    border-radius: 10px;\r\n}\r\n\r\n#totalProjectsCard, #totalTasksCard, #totalOverdueCard {\r\n    grid-row: 1/2;\r\n    text-align: center;\r\n    padding-top: 20px;\r\n    padding-bottom: 20px;\r\n}\r\n\r\n#totalOverdueCard > * {\r\n    color: #ED533E;\r\n}\r\n\r\n#totalOverdueCard {\r\n    border: 1px #ED533E90 solid;\r\n}\r\n\r\n.totalCardTitle {\r\n    color: #9fa2b4;\r\n    font-size: 12pt;\r\n    font-weight: bold;\r\n    letter-spacing: 0.5px;\r\n    padding-bottom: 20px;\r\n}\r\n\r\n.totalCardNumber {\r\n    color: #333;\r\n    font-size: 24pt;\r\n    font-weight: bold;\r\n}\r\n\r\n#recentContainer {\r\n    grid-row: 2/3;\r\n    grid-column: 1/4;\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: space-between;\r\n\r\n}\r\n\r\n#recentProjectsCard, #recentTasksCard {\r\n    width: 48.5%;\r\n    min-width: 300px;\r\n    padding: 20px;\r\n    margin-bottom: 40px;\r\n}\r\n\r\n.recentCardTitle {\r\n    font-size: 12pt;\r\n    color: #333;\r\n    font-weight: bold;\r\n    letter-spacing: 0.5px;\r\n}\r\n\r\n.recentCardTitleContainer {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding-bottom: 10px;\r\n}\r\n\r\n.recentCardTitleContainer a {\r\n    font-size: 10pt;\r\n    cursor: pointer;\r\n}\r\n\r\n.recentCardTitleContainer a:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n.recentCardSubtitle {\r\n    font-size: 10pt;\r\n    color: #9FA2B4;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.project-card-list, .task-list-div {\r\n    display: flex;\r\n    padding-top: 20px;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    font-size: 11pt;\r\n    padding-bottom: 20px;\r\n    border-bottom: 1px #c8cbd8 solid;\r\n}\r\n\r\n.project-card-list-title, .task-list-div-title {\r\n    color: #333;\r\n    font-weight: bold;\r\n    cursor: pointer;\r\n}\r\n\r\n.project-card-list-num {\r\n    color: #9FA2B4;\r\n}\r\n\r\n.task-list-div-title-container {\r\n    display: grid;\r\n    grid-template-columns: 1rem auto auto;\r\n    gap: 0.5em;\r\n}\r\n\r\n.task-list-div-title {\r\n    padding-right: 0;\r\n    margin-left: 10px;\r\n}\r\n\r\n.task-project-subtitle {\r\n    color: #9FA2B4;\r\n    font-size: 10pt;\r\n    margin-left: 0;\r\n}\r\n\r\ninput[type=\"checkbox\"] {\r\n    -webkit-appearance: none;\r\n    appearance: none;\r\n    background-color: #fff;\r\n    /* Not removed via appearance */\r\n    margin: 0;\r\n    font: inherit;\r\n    color: #8bc34a;\r\n    width: 1.5em;\r\n    height: 1.5em;\r\n    border: 1px solid #DFE0EB;\r\n    border-radius: 1em;\r\n    transform: translateY(-0.15em);\r\n    display: grid;\r\n    place-content: center;\r\n    cursor: pointer;\r\n}\r\n\r\ninput[type=\"checkbox\"]::before {\r\n    content: '';\r\n    width: 0.65em;\r\n    height: 0.65em;\r\n    transform: scale(0);\r\n    transition: 120ms transform ease-in-out;\r\n    box-shadow: inset 1em 1em #8bc34a;\r\n    transform-origin: bottom left;\r\n    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);\r\n}\r\n\r\ninput[type=\"checkbox\"]:checked::before {\r\n    transform: scale(1);\r\n}\r\n\r\ninput[type=\"checkbox\"]:focus {\r\n    outline: max(1px, 0.15em) solid #8bc34a;\r\n}\r\n\r\ninput[type=\"checkbox\"]:focus {\r\n    outline: max(2px, 0.15em) solid currentColor;\r\n    outline-offset: max(2px, 0.15em);\r\n  }\r\n\r\nspan.normal {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #F0F1F7;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #9FA2B4;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\nspan.urgent {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #ED533E;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #fff;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\nspan.complete {\r\n    background-color: #8bc34a;\r\n    color: #fff;\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n/* MODAL */\r\n\r\n.modal-hidden {\r\n    opacity: 0;\r\n    display: hidden;\r\n    visibility: hidden;\r\n}\r\n\r\n.modal {\r\n    position: fixed;\r\n    top: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 1rem;\r\n    background-color: rgba(0,0,0,0.5);\r\n    transition: all 0.1s ease-in;\r\n}\r\n\r\n.modal-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n\r\n.modal-close {\r\n    color: #ED533E;\r\n    cursor: pointer;\r\n}\r\n\r\n.modal-dialog {\r\n    width: 450px;\r\n    background-color: white;\r\n    border: 1px #DFE0EB solid;\r\n    border-radius: 10px;\r\n    padding: 30px;\r\n}\r\n\r\n.modal-title {\r\n    font-size: 12pt;\r\n    color: #333;\r\n    font-weight: bold;\r\n    letter-spacing: 0.5px;\r\n    padding-bottom: 40px;\r\n}\r\n\r\n#new-project-form, #new-task-form {\r\n    display: grid;\r\n    grid-template-columns: auto auto;\r\n    \r\n}\r\n\r\n#new-project-form label, #new-task-form label {\r\n    grid-column: 1/1;\r\n    color: #333;\r\n    font-size: 10pt;\r\n    font-weight: bold;\r\n    letter-spacing: 0.5px;\r\n}\r\n\r\n\r\n#new-project-form input, \r\n#new-project-form textarea, \r\n#new-task-form input, \r\n#new-task-form textarea,\r\n#new-task-form select {\r\n    grid-column: 2/2;\r\n    padding: 10px 5px;\r\n    margin-bottom: 20px;\r\n    border-radius: 5px;\r\n    border: 1px #9FA2B470 solid;\r\n}\r\n\r\n#new-project-buttons, #new-task-buttons {\r\n    display: flex;\r\n    font-weight: bold;\r\n    justify-content: space-between;\r\n    grid-column: 1/3;\r\n}\r\n\r\ninput[type=submit] {\r\n    background-color: #fff;\r\n    color: #8bc34a;\r\n    border: 1px #8bc34a solid !important;\r\n    cursor: pointer;\r\n}\r\n\r\ninput[type=submit]:hover {\r\n    background-color: #8bc34a;\r\n    color: #fff;\r\n}\r\n\r\ninput[type=reset] {\r\n    background-color: #fff;\r\n    color: #ED533E;\r\n    border: 1px #ED533E solid !important;\r\n    cursor: pointer;\r\n}\r\n\r\ninput[type=reset]:hover {\r\n    background-color: #ED533E;\r\n    color: #fff;\r\n}\r\n\r\n#submit-task-btn, #submit-project-btn, #cancel-task-btn, #cancel-project-btn {\r\n    width: 80%;\r\n    font-weight: bold;\r\n    margin: 20px auto;\r\n}\r\n\r\n#save-project, #cancel-project, #save-task, #cancel-task {\r\n    display: flex;\r\n    width: 100%;\r\n    justify-content: space-between;\r\n    \r\n}\r\n\r\nselect {\r\n    margin-bottom: 20px;\r\n}\r\n\r\ninput[type=\"radio\"] {\r\n    margin-right: 10px;\r\n}\r\n\r\n.radios > label {\r\n    margin-right: 20px;\r\n    font-size: 10pt;\r\n}\r\n\r\n\r\n\r\n/* TASKS TAB */\r\n/* content grid:\r\n2 rows: 3 grid lines\r\n3 columns: |  |  |  |\r\n*/\r\n\r\n#tasks-tab-card {\r\n    grid-row: 1/4;\r\n    grid-column: 1/4;\r\n    padding-top: 20px;\r\n    padding-bottom: 40px;\r\n}\r\n\r\n.table {\r\n    width: 100%;\r\n    font-size: 11pt;\r\n}\r\n\r\n\r\n.thead {\r\n    text-align: left;\r\n    text-transform: uppercase;\r\n    font-size: 10pt;\r\n    color: #333;\r\n    font-weight: bold;\r\n\r\n}\r\n\r\n.tr {\r\n    display: grid;\r\n    grid-template-columns: 7% 20% 20% 20% 20% auto;\r\n    padding: 20px;\r\n    border-bottom: 1px #DFE0EB solid;\r\n}\r\n\r\n.col-1 {\r\n    grid-column: 1/2;\r\n}\r\n\r\n.col-2 {\r\n    grid-column: 2/3;\r\n}\r\n\r\n.col-3 {\r\n    grid-column: 3/4;\r\n}\r\n\r\n.col-4 {\r\n    grid-column: 4/5;\r\n}\r\n\r\n.col-5 {\r\n    grid-column: 5/6;\r\n}\r\n\r\n.col-6 {\r\n    grid-column: 6/7;\r\n}\r\n\r\n.td {\r\n    color: #333333;\r\n}\r\n\r\n.todo-title {\r\n    font-weight: bold;\r\n}\r\n\r\ndiv.tr.urgent > * {\r\n    color: #ED533E;\r\n}\r\n\r\ndiv.tr.complete > * {\r\n    color: #8bc34a;\r\n}\r\n\r\ndiv.todo-row:hover {\r\n    background-color: #c0c0c033;\r\n}\r\n\r\n.hidden {\r\n    display: none;\r\n}\r\n\r\n.expand-container {\r\n    grid-column: 2/6;\r\n    padding: 20px 0px;\r\n    font-size: 10pt;\r\n}\r\n\r\n.expand-title {\r\n    display: inline-block;\r\n    font-weight: bold;\r\n    width: 150px;\r\n    text-transform: uppercase;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.expand-content {\r\n    display: inline-block;\r\n    width: calc(100% - 150px);\r\n}\r\n\r\n.expand-content.normal {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #F0F1F7;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #9FA2B4;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n.expand-content.urgent {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #ED533E;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #fff;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n.expand-content.complete {\r\n    background-color: #8bc34a;\r\n    color: #fff;\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n#edit-btn {\r\n    margin-bottom: 10px;\r\n    font-size: 11pt;\r\n    color: #3E81ED;\r\n    cursor: pointer;\r\n    border: none;\r\n}\r\n\r\n#delete-btn {\r\n    font-size: 11pt;\r\n    color: #ED533E;\r\n    cursor: pointer;\r\n    border: none;\r\n}\r\n.ending-cell button {\r\n    padding: 2px 8px;\r\n    background: none;\r\n    border-radius: 10px;\r\n}\r\n\r\n.priority-td {\r\n    min-width: 100px;\r\n}\r\n\r\n.priority-td > .normal {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #F0F1F7;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #9FA2B4;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n.priority-td > .urgent {\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n    background-color: #ED533E;\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n    color: #fff;\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n.priority-td > .complete {\r\n    background-color: #8bc34a;\r\n    color: #fff;\r\n    width: 100px;\r\n    min-width: 100px;\r\n    height: 25px;\r\n\r\n    border-radius: 5px;\r\n    font-size: 10pt;\r\n    text-transform: uppercase;\r\n    font-weight: bold;\r\n\r\n    text-align: center;\r\n    padding-top: 4px;\r\n}\r\n\r\n\r\n/* EDIT TASK PANEL */\r\n#edit-task-panel {\r\n    position: fixed;\r\n    width: 500px;\r\n\r\n    padding: 20px;\r\n    left: calc(50% - 250px);\r\n    top: calc(50% - 250px);\r\n    color: #252733;\r\n}\r\n\r\n#edit-task-panel-header {\r\n    font-size: 16pt;\r\n    margin: 10px 0px;\r\n}\r\n\r\n#edit-task-form {\r\n 